//import router from '@/router' //VueRouter를 불러온다.
//import store from '@/store'
import axios from 'axios'
import uri from './uri'
//import config from '@/config'

const instance = axios.create({
	baseURL: uri.domain,
	withCredentials: true,
	timeout: 10000
});
//차후 작업할때는 dothome연결 후 진행 예정 post , get 통신 
export default {
	getData(Testdata){
		//return instance.get(uri.SETTING_AUTH_DETAIL)
		return instance.get(uri.testGet, {params:{data1:Testdata, data2:Testdata,data3:Testdata,data4:Testdata}})
		//return instance.get(uri.COMM_INFO, {params:{
		//				searchUserCateCd: searchUserCateCd,
    //        searchWord: searchWord,}})
	},
	postData(Testdata){
		// return instance.post(uri.testPost, {params:{data1:Testdata}})
		return instance.post(uri.testPost, {data1:Testdata, data2:Testdata,data3:Testdata,data4:Testdata}) 
},
}