import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import { loadFonts } from './plugins/webfontloader'
import uri from "@/network/uri";
import networkManager from "@/network/networkManager";
import axios from 'axios';

axios.defaults.baseURL = 'http://mydothomefree.dothome.co.kr/';
axios.defaults.withCredentials = true;

const app = createApp(App)
loadFonts()

app.config.globalProperties.$uri = uri;
app.config.globalProperties.$networkManager = networkManager;
app.config.globalProperties.$axios = axios; // axios를 전역 속성으로 추가

app.use(router)
app.use(store)
app.use(vuetify)
app.mount('#app')
