// import Vue from 'vue'
// import VueRouter from 'vue-router' 
// Vue.use(VueRouter)
import { createRouter, createWebHistory } from 'vue-router';



const routes = [
  {
    path: '/',
    component: () => import('@/views/layout/default/Index.vue'),
    children: [
        {
            path: '',
            name: 'Home',
            component: () => import('@/views/components/testPage/Home.vue'), // 해당 페이지 방문 시 vue 파일을 로드함.
            props: true,
        },
        {
          path: '/about',
          name: 'About',
          component: () => import('@/views/components/testPage/About.vue'), // 해당 페이지 방문 시 vue 파일을 로드함.
          props: true,
      },
      {
        path: '/getTest',
        name: 'getTest',
        component: () => import('@/views/components/testPage/getPage.vue'), // 해당 페이지 방문 시 vue 파일을 로드함.
        props: true,
    },

    ]
  }
  // 추가 라우트를 여기에 정의합니다.

]

// 라우터 인스턴스 생성
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router
